import { AdalConfig, adalGetToken, AuthenticationContext } from 'react-adal';

const prodAppId = 'c23fa0bc-275e-45a9-a518-b33893062e6b';
const localhostAppId = '9fa8cac6-4668-449a-9232-f48c60a057c1';
const endpoint = 'https://wbengineering.sharepoint.com/';

const adalConfig: AdalConfig = {
    cacheLocation: 'localStorage',
    clientId: process.env.NODE_ENV === 'production' ? prodAppId : localhostAppId,
    endpoints: {
        api:endpoint
    },
    postLogoutRedirectUri: window.location.origin,
    tenant: 'wbengineering.onmicrosoft.com'
};

class AdalContext {
    private authContext: AuthenticationContext;
    
    constructor() {
        this.authContext = new AuthenticationContext(adalConfig);
    }

    get AuthContext() {
        return this.authContext;
    }

    public GetToken(): Promise<string | null> {
        return adalGetToken(this.authContext, endpoint);
    }

    public LogOut() {
        this.authContext.logOut();
    }
}

const adalContext: AdalContext = new AdalContext();
export default adalContext;


